<template>
  <div>
    <v-app>
      <v-navigation-drawer
        fixed
        :clipped="$vuetify.breakpoint.mdAndUp"
        app
        v-model="drawer"
      >
        <v-list dense v-if="loggedIn">
          <v-list-tile
            v-for="(item, i) in menu"
            :key="i"
            :to="{path: item.path}"
          >
            <v-list-tile-action>
              <v-icon v-html="item.icon"></v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.label"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar
        color="primary" dark
        app
        :clipped-left="$vuetify.breakpoint.mdAndUp"
        fixed
      >
        <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
          <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
          <span class="hidden-sm-and-down" v-text="appTitle"></span>
        </v-toolbar-title>
        <!-- <v-text-field
          flat
          solo-inverted
          prepend-icon="search"
          label="Search"
          class="hidden-sm-and-down"
        ></v-text-field> -->
        <v-spacer></v-spacer>
        <v-menu v-if="!loggedIn" open-on-hover top offset-y>
          <v-btn icon large slot="activator">
            <v-avatar size="32px" tile>
              <img
                src="../../assets/user-icon.svg"
                alt="Login"
              >
            </v-avatar>
          </v-btn>
          <v-list>
            <v-list-tile :key="items.login.title" @click.stop="dialogLogin = true">
              <v-list-tile-title v-text="items.login.title"></v-list-tile-title>
            </v-list-tile>
            <v-list-tile :key="items.register.title" @click.stop="dialogRegister = true">
              <v-list-tile-title v-text="items.register.title"></v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
        <v-dialog v-model="dialogLogin" max-width="800px">
          <v-card>
            <v-card-text>
              <v-card-title>
                <span class="headline">로그인</span>
              </v-card-title>
              <v-alert outline color="error" icon="warning" :value="error" v-text="error"></v-alert>
              <v-form v-model="valid" ref="formLogin" lazy-validation>
                <v-text-field
                  prepend-icon="person"
                  label="E-mail"
                  v-model="email"
                  :rules="emailRules"
                  required
                  type="text"
                  placeholder="example@gmail.com"
                ></v-text-field>
                <v-text-field
                  prepend-icon="lock"
                  label="Password"
                  v-model="password"
                  :rules="passwordRules"
                  :counter="10"
                  required
                  type="password"
                  placeholder="비밀번호"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn depressed large color="primary" @click.prevent="login">로그인</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogRegister" max-width="800px">
          <v-card>
            <v-card-text>
              <v-card-title>
                <span class="headline">가입</span>
              </v-card-title>
              <v-alert outline color="error" icon="warning" :value="error" v-text="error"></v-alert>
              <v-form v-model="valid2" ref="formRegister" lazy-validation>
                <v-text-field
                  prepend-icon="face"
                  label="Name"
                  v-model="register.name"
                  :rules="nameRules"
                  required
                  type="text"
                  placeholder="이름"
                ></v-text-field>
                <v-text-field
                  prepend-icon="person"
                  label="Nick name"
                  v-model="register.apellidos"
                  required
                  type="text"
                  placeholder="별명"
                ></v-text-field>
                <v-text-field
                  prepend-icon="room"
                  label="Age"
                  v-model="register.edad"
                  required
                  type="text"
                  placeholder="나이"
                ></v-text-field>
                <v-text-field
                  prepend-icon="person"
                  label="E-mail"
                  v-model="register.email"
                  :rules="emailRules"
                  required
                  type="text"
                  placeholder="example@gmail.com"
                ></v-text-field>
                <v-text-field
                  prepend-icon="lock"
                  label="Password"
                  v-model="register.password"
                  :rules="passwordRules"
                  :counter="10"
                  required
                  type="password"
                  placeholder="비밀번호"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn depressed large color="primary" dark @click.prevent="addUser">등록</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-content>
           <v-container v-if="!loggedIn" fill-height>
            <v-layout align-center>
            <v-flex>
                <h2 class="display-3">광성교회 교육부서 </h2>
                <!-- <span class="subheading" v-text="msg"></span> -->
                <v-divider class="my-3"></v-divider>
                <div class="title mb-3">오른쪽 상단을 눌러 로그인을 해주세요</div>
            </v-flex>
            </v-layout>
        </v-container>
        <v-container fluid>
          <router-view v-if="loggedIn"></router-view>
        </v-container>
      </v-content>
      <v-footer
        app
        fixed
        color="primary" dark>
        <span>&copy; 2019 광성교회</span>
      </v-footer>
    </v-app>
  </div>
</template>

<script>
import apiService from '@/Services/ApiService'

export default {
  mounted () {
    this.path = this.$route.path
  },
  data () {
    return {
      tabs: null,
      dialogLogin: false,
      dialogRegister: false,
      drawer: null,
      appTitle: ' 메뉴 ',
      loginTitle: '로그인',
      path: '/',
      menu: [
        {path: '/api/home', label: '그래프', icon: 'show_chart'},
        {path: '/api/members', label: '구성원', icon: 'list'},
        {path: '/api/events', label: '부서 일정', icon: 'event'},
        {path: '/api/worship', label: ' 예배 정보', icon: 'info'},
        {path: '/api/attendee', label: '주일 보고', icon: 'description'},
        {path: '/logout', label: '로그아웃', icon: 'lock'}
      ],
      items: {
        login: {title: '로그인'},
        register: {title: '가입'}
      },
      register: {
        name: '',
        apellidos: '',
        edad: '',
        email: '',
        password: ''
      },
      valid: true,
      valid2: true,
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ],
      password: '',
      passwordRules: [
        v => !!v || 'Password is required',
        v => v.length <= 10 || 'Password must be less than 10 characters'
      ],
      error: false
    }
  },
  computed: {
    loggedIn: {
      get: () => {
        return Boolean(localStorage.getItem('user'))
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.path = to.path
    }
  },
  methods: {
    login () {
      if (this.$refs.formLogin.validate()) {
        apiService.userLogin({
          email: this.email,
          password: this.password
        })
          .then(response => this.loginSuccessful(response))
          .catch(response => this.loginFailed(response))
      }
    },
    loginSuccessful (response) {
      this.error = false
      localStorage.user = JSON.stringify(response.data)
      this.$router.replace('/api/home')
      this.$router.go('/api/home')
    },
    loginFailed (response) {
      this.error = response.response.data.message || 'Error Login'
      delete localStorage.token
    },
    addUser () {
      if (this.$refs.formRegister.validate()) {
        apiService.addUser({
          name: this.register.name,
          apellidos: this.register.apellidos,
          edad: this.register.edad,
          email: this.register.email,
          password: this.register.password
        })
          .then(response => this.loginSuccessful(response))
          .catch(response => this.loginFailed(response))
      }
    }
  }
}
</script>

<style>

</style>



